  // import { apiShopDetail, apiShoppingAdd, apiUserInfo, apiUserBind, apiUserInfoSave, apiUserLogin } from '@/service/index'
  import interfaceurl from '@/utils/interface.js'
  //引入store的内容
  import {
  	mapState,
  	mapMutations,
  	mapActions
  } from 'vuex'
  export default {
  	computed: mapState(['goodsDetail']),
		data() {
			return {
				id: '',
				confirmModal: false,
				indicatorDots: false,
				autoplay: true,
				interval: 2000,
				duration: 500,
				detail: {
				  goods_name: '红牡丹抽色润颜霜 均匀亮白十全十美套盒'
				},
				shopNum: 1,
				type: 1,
						bannerList: ['/static/banner.png'],
				shopItem: {},
				invitation_code: '',
				userInfo: {},
				is_cash_back: '0'
			}
		},
    onShareAppMessage(res) {
      return {
        title: '一米一粉',
        path: '/pages/shop-detail/shop-detail?id='+this.id + '&code='+ this.invitation_code,
        imageUrl: '/static/share-banner.png'
      }
    },
    onLoad(options) {
      console.log('detail')
      console.log(options)
      return
      this.id = options.id
      this.is_cash_back = options.is_cash_back
      const login = uni.getStorageSync('login');
      const self = this
      if(!login) {
        console.log('aaa')
        uni.login({
          provider: 'weixin',
          success: function (res) {
            apiUserLogin({
              code: res.code
            })
            .then((res) => {
              if(res.code == 0) {
                uni.setStorageSync('token', res.data.token);
                uni.setStorageSync('login', true);
                if(options.code) {
                  console.log('code')
                  apiUserBind({
                    scene_value: options.code
                  })
                  .then((res) => {
                     
                  })
                }
                self.getShopDetail()
                self.getUserCode()
              }
            })
          }
        });
      }else {
        console.log('bbb')
        if(options.code) {
          apiUserBind({
            scene_value: options.code
          })
          .then((res) => {
             
          })
        }
        this.getShopDetail()
        this.getUserCode()
      }
    },
    onShow() {
		console.log('goodsDetail',this.goodsDetail)
      const value = wx.getStorageSync('userInfo')
      if(value){
        this.userInfo = value
      }
    },
		methods: {
			addCart() {
				let that = this;
				let params = {
					code: '' ,//商品sku
					quantity: 1
				}
				interfaceurl.checkAuth(interfaceurl.cartCreate, {}, false).then((res) => {
					that.bannerList = res.data
				});
			},
      getUserInfo(e) {
        console.log(e)
        this.userInfo = e.detail.userInfo
        if(e.detail.rawData){
          this.userInfo = JSON.parse(e.detail.rawData)
        }
        this.saveUserInfo()
      },
      saveUserInfo() {
        apiUserInfoSave({
          nickName: this.userInfo.nickName,
          gender: this.userInfo.gender,
          avatarUrl: this.userInfo.avatarUrl
        })
        .then((res) => {
          if(res.code == 0) {
            wx.setStorageSync('userInfo', this.userInfo)
          }
        })
      },
      getUserCode() {
        apiUserInfo()
        .then((res) => {
          if(res.code == 0) {
            this.invitation_code = res.data.invitation_code
          }
        })
      },
      reduceNum() {
        if(this.shopNum <= 1) {
         this.shopNum = 1
        }
        this.shopNum--
      },
      addNum() {
        this.shopNum++
      },
      handlePay(type) {
				if(type == 1 && this.is_cash_back == 1) {
					uni.showToast({
					  title: '全返商品请直接购买',
					  icon: 'none',
					  duration: 2000
					});
					return
				}
        this.type = type
        this.confirmModal = true
      },
      shoppingPage() {
        uni.switchTab({
          url: '/pages/shopping/shopping'
        });
      },
      handleConfirm() {
        this.confirmModal = false
        if(this.type == 1) {
          apiShoppingAdd({
            goods_id: this.id,
            buy_number: this.shopNum
          })
          .then((res) => {
            if(res.code == 0) {
              uni.showToast({
                title: '已加入购物车',
                icon: 'none',
                duration: 2000
              });
            }
          })
        }else{
          this.confrimOrderPage()
        }
      },
      getShopDetail() {
        apiShopDetail({
          goods_id: this.id
        })
        .then((res) => {
          if(res.code == 0) {
            this.bannerList = res.data.goods_banner
            this.detail = res.data
            this.detail.goods_details = this.handleImgHtml(res.data.goods_details)
          }
        })
      },
      handleImgHtml (str) {
        var richtext=  str
        const regex = new RegExp('<img', 'gi');
        richtext= richtext.replace(regex, `<img style="max-width: 100%;margin: 0px;padding:0px;vertical-align: top;"`);
        return richtext;
      },
			confrimOrderPage() {
        const shopItem = {
          goods_id: this.id,
          goods_price: this.detail.goods_price,
          goods_name: this.detail.goods_name,
          goods_cover_img: this.detail.goods_cover_img,
          is_cash_back: this.is_cash_back,
          shopNum: this.shopNum,
          cart_id: ''
        }
        let shopList = []
        shopList.push(shopItem)
        uni.setStorageSync('shopList', shopList)
				uni.navigateTo({
				  url: '/pages/confirm-order/confirm-order'
				});
			}
		}
	}
